@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Coda+Caption:wght@800&display=swap);
body{  font-family: 'Nunito', sans-serif;
}
.strongFont{
  font-family: 'Coda Caption', sans-serif;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
td{
width:90px;height:60px;background-color:#fff;border:1px solid green;
}
.slider {
  position:absolute;
  top:0;
  right:0px !important;
  background-color:#a8d7ea;
  color:#fff;
  transition:1s ease;
}
.training-select{
  left:0;
  position:absolute;height:84%;width:80;
  background-color:#4264ea;-ms-writing-mode:tb-rl;writing-mode:vertical-rl;
  -webkit-text-orientation: mixed;
          text-orientation: mixed;
  color:#fff;
}
.blank-cell{
  background-color: #f5f4fa !important;border:none;
}
.signup-form {
  margin-left:10px;
  margin-right: 10px;
  border-radius: 6px;
  max-width:388px !important;
  border:2px solid #fff;
  outline:none;
  padding:5px;
  width:368px;
  height:60px;
  font-size: 32px !important;
}
.scrollbar::-webkit-scrollbar {
  background-color:transparent;
  width:16px
}

/* background of the scrollbar except button or resizer */
.scrollbar::-webkit-scrollbar-track {
  background-color:transparent;
}
.scrollbar::-webkit-scrollbar-track:hover {
  background-color:transparent;
}

/* scrollbar itself */
.scrollbar::-webkit-scrollbar-thumb {
  background-color:#babac0;
  border-radius:16px;
  border:5px solid #eeeeee
}
.scrollbar::-webkit-scrollbar-thumb:hover {
  background-color:#a0a0a5;
  border:4px solid #f4f4f4
}

.fadedshort{ /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
  animation: fadein 0.5s;
}

@keyframes fadein {
  from {
    opacity:0;
  }
  to {
    opacity:1;
  }
}
